.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 2rem;
}

.App-intro {
  font-size: large;
}

.Error-text {
  color: red;
}

.Emojis-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.Emoji-display {
  margin: 1em 4%;
  padding: 0.5em;
  border: 3px solid lightgray;
  border-radius: 10px;
  background-color: white;
  font-size: 1.5rem;
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: 25%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.Emoji-display:hover {
  background-color: lightgray;
  cursor: pointer;
}

.Emoji-image {
  width: 2em; 
}

.Emoji-copy {
  color: white;
  font-size: 1rem;
}

.Emoji-search {
  max-width: 50%;
  margin: 2em auto;
}

.Link-image {
  width: 1em;
  color: white;
}

.Option {
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.Option:hover .Option-copy {
  display: block;
}

.Option-image {
  position: absolute;
  width: 2em;
  height: 2em;
}

.Option-copy {
  position: absolute;
  right: 0;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  display: none;
}

@media only screen and (max-width: 870px) {
  .Emoji-search {
    max-width: 90%;
  }


  .Emoji-display {
    width: 92%;
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



